import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { AngularFireDatabase, FirebaseListObservable } from 'angularfire2/database';

import jsPDF from 'jspdf'
declare let jsPDF;

import { ViewChild } from '@angular/core';
import { Slides } from 'ionic-angular';

@Component({
  selector: 'page-beoordeling',
  templateUrl: 'beoordeling.html'
})

export class BeoordelingPage {

	forms: FirebaseListObservable<any>;
  @ViewChild(Slides) slides: Slides;

  constructor(public navCtrl: NavController, af: AngularFireDatabase, private alertCtrl: AlertController) {
	   this.forms = af.list('/beoordeelforms');
  }

  initialSchijt(){
    console.log('test');
  }

  rate = {}
  submitForm(id) {
          if(id.datum == null)
          {
              let date = new Date;
              let year = date.getFullYear();
              let month = date.getMonth() + 1;
              let day = date.getDate();
              let datum = year + '-' + month + '-' + day;

              this.rate['datum'] = datum;
              console.log(1);
          }
          if(id.akkoord == true)
          {
            console.log(this.rate);
            this.forms.push(this.rate);
          }
          else{
            this.presentAlert();
          }
      }

  public download(id) {
          console.log(id.naam);

          let pageWidth = 8.5,
          lineHeight = 1.2,
          margin = 0.5,
          maxLineWidth = pageWidth - margin * 2,
          fontSize = 14,
          ptsPerInch = 72,
          oneLineHeight = fontSize * lineHeight / ptsPerInch,
          text = 'Naam: \n' + id.naam + '\n\n' +
              'BPV Docent: \n' + id.bpvdocent + '\n\n' +
              'BPV Bedrijf: \n' + id.bpvbedrijf + '\n\n' +
              'Praktijkopleider: \n' + id.praktijkopleider + '\n\n' +
              'Datum: \n' + id.datum + '\n\n' +
              'Beoordeling: \n' + id.beoordeling + '\n\n';

          let doc = new jsPDF({
              unit: 'in',
              lineHeight: lineHeight
            }).setProperties({ title: 'Gespreksformulier' });

          let textLines = doc
              .setFont('helvetica', 'neue')
              .setFontSize(fontSize)
              .splitTextToSize(text, maxLineWidth);

          //Create de tekst en pdf format
          doc.text(textLines, margin, margin + 2 * oneLineHeight);

          let textHeight = textLines.length * fontSize * lineHeight / ptsPerInch;
              doc
                  .setFontStyle('bold')
                  .text('Gespreksformulier:', margin, margin + oneLineHeight);


        //maakt rectangles
          doc.setLineWidth(0);
          doc.rect(margin, textHeight + 3, 3, oneLineHeight + 1);
          doc.text('Handtekening Student', margin, textHeight + 2.5);
		  doc.rect(margin + 4, textHeight + 3, 3, oneLineHeight + 1);
		  doc.text('Handtekening Begeleider', margin + 4, textHeight + 2.5);
          doc.save('Test.pdf');
      }

      presentAlert() {
      let alert = this.alertCtrl.create({
        title: 'Akkoord',
        subTitle: 'Ga akkoord met het formulier',
        buttons: ['Sluit']
      });
      alert.present();
}

slideChanged() {
  let currentIndex = this.slides.getActiveIndex();
  let isBeg = this.slides.isBeginning();
  let isEnd = this.slides.isEnd();
  console.log(isEnd);
  console.log('Current index is', currentIndex);
  if(isBeg === true){
    document.getElementById('backButton').style.visibility = 'hidden';
    console.log('is begin');
  }
  else if(isEnd === true){
    document.getElementById('forwardButton').style.visibility = 'hidden';
    console.log('is einde');
  }
  else{
    document.getElementById('backButton').style.visibility = 'visible';
    document.getElementById('forwardButton').style.visibility = 'visible';
  }
}

vorigeSlide() {
  this.slides.lockSwipeToPrev(false);
  this.slides.slidePrev(200, true);
  this.slides.lockSwipeToPrev(true);
}
nextSlide() {
  this.slides.lockSwipeToNext(false);
  this.slides.slideNext(200, true);
  this.slides.lockSwipeToNext(true);
}

}



